body {
    background-image: radial-gradient(#0e272c 5%, #000f1e);
}

.tictactoe {
    width: 100%;
    max-width: 640px;
    min-width: 360px;
    margin: 50px auto;
}

@media (max-width: 1400px) {
    .tictactoe {
        max-width: 500px;
        margin: 20px auto;
    }
}

@media (max-width: 575px) {
    .tictactoe {
        margin-top: 0px;
        max-width: 320px;
        min-width: 50%;
    }
}
